*,
::before,
::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  font-family: Arial, Helvetica, sans-serif;
}

nav {
  position: absolute;
  top: 0;
  width: 100%;
  height: 50px;
  background: #1f1f1f;
}
.liste {
  list-style-type: none;
  background: #1f1f1f;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  font-size: 22px;
}
.liste a {
  text-decoration: none;
  color: #f1f1f1;
}
.items {
  margin: 0 15px;
  text-transform: uppercase;
  color: #f1f1f1;
  cursor: pointer;
}
.btn {
  display: none;
  position: absolute;
  right: 10px;
  top: 7px;
  padding: 5px;
  color: #000;
  font-size: 18px;
}

@media screen and (max-width: 500px) {
  .liste {
    flex-direction: column;
    height: auto;
  }
  .items:nth-child(1) {
    border-top: 1px solid rgba(255, 255, 255, 0.6);
    margin-top: 50px;
  }
  .items {
    height: 70px;
    width: 100%;
    border-bottom: 1px solid rgba(255, 255, 255, 0.6);
    line-height: 70px;
    text-align: center;
  }
  .btn {
    display: block;
  }
}

.home-title {
  font-size: 60px;
  margin: 100px 0 40px;
  text-align: center;
}
.container-cards {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
  max-width: 1300px;
  margin: 0 auto 0;
}

.card {
  max-width: 300px;
  min-width: 300px;
  padding: 10px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  margin: 10px;
  cursor: pointer;
  min-height: 200px;
  text-align: left;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.card h2 {
  font-size: 25px;
  margin-bottom: 10px;
}
.card p {
  font-size: 18px;
}

.title-form {
  text-align: center;
  font-size: 55px;
  margin: 70px 0 0;
}
.container-form {
  padding: 10px;
  max-width: 900px;
  margin: 30px auto 0;
  display: flex;
  flex-direction: column;
}
.container-form label {
  font-size: 20px;
}
.container-form input,
.container-form textarea {
  padding: 10px;
  margin-bottom: 25px;
  font-size: 18px;
}
.container-form textarea {
  min-height: 200px;
  font-family: Arial, Helvetica, sans-serif;
}
.container-form button {
  padding: 10px 15px;
  width: 200px;
  background: #1f1f1f;
  font-size: large;
  color: #f1f1f1;
  border: none;
  border-radius: 3px;
  cursor: pointer;
}

.article-content {
  max-width: 700px;
  min-width: 300px;
  margin: 100px auto;
}
.article-content h2 {
  font-size: 45px;
  margin-bottom: 20px;
}
.article-content p {
  font-size: 25px;
}

.container-contact {
  max-width: 700px;
  margin: 100px auto 0;
  font-size: 25px;
}
.container-contact h1 {
  font-size: 50px;
  margin-bottom: 10px;
}
.container-contact p:nth-child(4) {
  margin-top: 60px;
}

